import { Object } from 'fabric/fabric-impl';
import React, { useState } from 'react';
import { CurvedLineType } from '../fabric/curvedLine';
import InputBox from './InputBox';

interface Props {
  item: CurvedLineType;
  renderAll: () => void;
}

const LineController = ({ item, renderAll }: Props) => {
  const { canvas } = item;
  const [amplitude, setAmplitude] = useState(item.amplitude);
  const [waveLength, setWaveLength] = useState(item.waveLength);
  const [isFilled, setIsFilled] = useState(item.isFilled);
  const [centerX, setCenterX] = useState(item.centerX);
  const [cropLeft, setCropLeft] = useState(item.cropLeft);
  const [cropRight, setCropRight] = useState(item.cropRight);
  const [filledY, setFilledY] = useState(item.filledY);

  return (
    <div className="controller-container">
      <div className="controller-main">
        <InputBox
          label="진폭"
          className="controller-input"
          name="amplitude"
          type="range"
          value={amplitude}
          min={-(canvas.height / 2)}
          max={canvas.height / 2}
          onChange={(event: React.ChangeEvent) => {
            const { value } = event.target as HTMLInputElement;
            item.set('amplitude', value);
            setAmplitude(value);
            renderAll();
          }}
        />
        <InputBox
          label="왼쪽 범위"
          className="controller-input"
          name="cropLeft"
          type="range"
          value={cropLeft}
          min={0}
          max={canvas.width - cropRight}
          onChange={(event: React.ChangeEvent) => {
            const { value } = event.target as HTMLInputElement;
            item.set('cropLeft', value);
            setCropLeft(value);
            renderAll();
          }}
        />
        <InputBox
          label="오른쪽 범위"
          className="reverse"
          name="cropRight"
          type="range"
          value={cropRight}
          min={0}
          max={canvas.width - cropLeft}
          onChange={(event: React.ChangeEvent) => {
            const { value } = event.target as HTMLInputElement;
            item.set('cropRight', value);
            setCropRight(value);
            renderAll();
          }}
        />
        <InputBox
          label="채우기"
          className="controller-input"
          name="isFilled"
          type="checkbox"
          checked={isFilled}
          onChange={(event: React.ChangeEvent) => {
            const { checked } = event.target as HTMLInputElement;
            item.set('isFilled', checked);
            setIsFilled(checked);
            renderAll();
          }}
          onClick={() => {
            setIsFilled((prev: boolean) => {
              item.set('isFilled', !prev);
              renderAll();
              return !prev;
            });
          }}
        />
      </div>
      <div className="controller-sub">
        {Number(amplitude) !== 0 && (
          <>
            <InputBox
              label="파장"
              className="controller-input"
              name="waveLength"
              type="range"
              value={waveLength}
              min={2}
              max={canvas.width * 2}
              step={10}
              onChange={(event: React.ChangeEvent) => {
                const { value } = event.target as HTMLInputElement;
                item.set('waveLength', value);
                setWaveLength(value);
                renderAll();
              }}
            />
            <InputBox
              label="가로 이동"
              className="controller-input"
              name="centerX"
              type="range"
              value={centerX}
              min={0}
              max={Math.floor(waveLength * 2.5)}
              onChange={(event: React.ChangeEvent) => {
                const { value } = event.target as HTMLInputElement;
                item.set('centerX', value);
                setCenterX(value);
                renderAll();
              }}
            />
          </>
        )}
        {isFilled && (
          <InputBox
            label="채우기 범위"
            className="controller-input"
            name="filledY"
            type="range"
            value={filledY}
            min={-canvas.height}
            max={canvas.height}
            onChange={(event: React.ChangeEvent) => {
              const { value } = event.target as HTMLInputElement;
              item.set('filledY', value);
              setFilledY(value);
              renderAll();
            }}
          />
        )}
      </div>
    </div>
  );
};

export default LineController;
