import React from 'react';
import { Tool } from '../hooks/useFabricCanvas';
import circle from '../assets/svg/circle.svg';
import rect from '../assets/svg/rect.svg';
import select from '../assets/svg/select.svg';
import curved from '../assets/svg/curved.svg';
import text from '../assets/svg/text.svg';

interface Props {
  current: Tool;
  changeTool: (tool: Tool) => void;
}

const ToolSelector = ({ current, changeTool }: Props) => {
  return (
    <div className="tools">
      <button
        className={`tool ${current === 'select' ? 'selected' : ''}`}
        onClick={() => {
          changeTool('select');
        }}
      >
        <img className="icon" src={select} alt="선택 도구" />
      </button>
      <button
        className={`tool ${current === 'circle' ? 'selected' : ''}`}
        onClick={() => {
          changeTool('circle');
        }}
      >
        <img className="icon" src={circle} alt="원 도구" />
      </button>
      <button
        className={`tool ${current === 'rect' ? 'selected' : ''}`}
        onClick={() => {
          changeTool('rect');
        }}
      >
        <img className="icon" src={rect} alt="사각형 도구" />
      </button>
      <button
        className={`tool ${current === 'text' ? 'selected' : ''}`}
        onClick={() => {
          changeTool('text');
        }}
      >
        <img className="icon" src={text} alt="텍스트 도구" />
      </button>
      <button
        className={`tool ${current === 'line' ? 'selected' : ''}`}
        onClick={() => {
          changeTool('line');
        }}
      >
        <img className="icon" src={curved} alt="곡선 도구" />
      </button>
    </div>
  );
};

export default ToolSelector;
