import { useState } from 'react';
import { fabric } from 'fabric';
import { Canvas, IEvent, Object as FabObject } from 'fabric/fabric-impl';
import CurevedLine from '../fabric/curvedLine';

interface Point {
  x: number;
  y: number;
}

export type Tool = 'circle' | 'rect' | 'text' | 'line' | 'select';

const useFabricCanvas = (width: number, height: number) => {
  const [canvas, setCanvas] = useState<Canvas>();
  const [tool, setTool] = useState<Tool>('select');
  const [currentSelected, setCurrentSelected] = useState<FabObject>();

  const init = (canvas: HTMLCanvasElement) => {
    setCanvas(new fabric.Canvas(canvas, { width, height }));
  };

  function renderAll() {
    canvas?.renderAll();
  }

  function changeCurrentSelected(item: FabObject) {
    setCurrentSelected(item);
  }

  function changeTool(tool: Tool) {
    setTool(tool);
    renderAll();
  }

  function addRect(point?: Point) {
    if (!canvas) return;
    const rect = new fabric.Rect({
      originX: 'center',
      originY: 'center',
      width: 50,
      height: 50,
      stroke: 'black',
      fill: undefined,
      left: point?.x,
      top: point?.y,
    });
    rect.set('type', 'rect');
    canvas.add(rect);
    canvas.setActiveObject(rect);
  }

  function addLine(point?: Point) {
    if (!canvas) return;
    const line = new CurevedLine(
      { x: 0, y: height / 2 },
      { x: 630, y: height / 2 },
      { left: point?.x, top: point?.y, originX: 'center', originY: 'center' }
    );
    line.set('type', 'line');
    canvas.add(line);
    canvas.setActiveObject(line);
  }

  function addCircle(point?: Point) {
    if (!canvas) return;
    const circle = new fabric.Circle({
      originX: 'center',
      originY: 'center',
      radius: 50,
      stroke: 'black',
      fill: undefined,
      left: point?.x,
      top: point?.y,
    });
    circle.set('type', 'circle');
    canvas.add(circle);
    canvas.setActiveObject(circle);
  }

  function addText(point?: Point) {
    if (!canvas) return;
    const text = new fabric.IText('텍스트를 입력하세요.', {
      editable: true,
      originX: 'center',
      originY: 'center',
      left: point?.x,
      top: point?.y,
    });
    text.set('type', 'text');
    canvas.add(text);
    canvas.setActiveObject(text);
  }

  function observer(event: string, callback: (options: IEvent) => void) {
    if (!canvas) return;

    // 이벤트 리스너를 매 렌더링 때마다 제거하고 새로 추가 해주어야 함
    canvas.on(event, callback);
  }

  function removeObserver(event: string) {
    if (!canvas) return;
    canvas.off(event);
  }

  function drawItem(point?: Point) {
    switch (tool) {
      case 'circle':
        addCircle(point);
        break;
      case 'rect':
        addRect(point);
        break;
      case 'text':
        addText(point);
        break;
      case 'line':
        addLine(point);
        break;
      case 'select':
        break;
      default:
        throw new Error(`invalid tool: ${tool}`);
    }
  }

  function toDataURL() {
    return canvas?.toDataURL() || '';
  }

  return {
    canvas,
    tool,
    currentSelected,
    init,
    renderAll,
    drawItem,
    changeTool,
    changeCurrentSelected,
    observer,
    removeObserver,
    toDataURL,
  };
};

export default useFabricCanvas;
