import { Object } from 'fabric/fabric-impl';
import React from 'react';
import CircleController from './CircleController';
import LineController from './LineController';
import RectController from './RectController';
import TextController from './TextController';

interface Props {
  currentSelected?: Object;
  renderAll: () => void;
}

const Controller = ({ currentSelected, renderAll }: Props) => {
  return (
    <div className="controller">
      {currentSelected?.type === 'circle' && (
        <CircleController item={currentSelected} renderAll={renderAll} />
      )}
      {currentSelected?.type === 'rect' && (
        <RectController item={currentSelected} renderAll={renderAll} />
      )}
      {currentSelected?.type === 'text' && (
        <TextController item={currentSelected} renderAll={renderAll} />
      )}
      {currentSelected?.type === 'line' && (
        <LineController item={currentSelected} renderAll={renderAll} />
      )}
    </div>
  );
};

export default Controller;
