import React, { useEffect, useRef } from 'react';
import Controller from '../components/Controller';
import DownloadBtn from '../components/DownloadBtn';
import ToolSelector from '../components/ToolSelector';
import useFabricCanvas from '../hooks/useFabricCanvas';
import CanvasBG from './CanvasBG';

const width = 630;
const height = 300;

function getNameWithNowDate(prefix: string) {
  const time = new Date();
  const yyyy = time.getFullYear();
  const mm = time.getMonth() > 8 ? time.getMonth() + 1 : `0${time.getMonth() + 1}`;
  const dd = time.getDate() > 9 ? time.getDate() : `0${time.getDate()}`;

  return `${prefix}${yyyy}${mm}${dd}`;
}

const DividerGenerator = () => {
  const canvasRef = useRef<HTMLCanvasElement>();
  const fabricCanvas = useFabricCanvas(width, height);
  const { tool, renderAll, currentSelected, changeTool, changeCurrentSelected } = fabricCanvas;

  // fabric 초기화
  useEffect(() => {
    const canvasEl = canvasRef.current;
    if (!canvasEl || fabricCanvas.canvas) return;

    fabricCanvas.init(canvasEl);
  }, [fabricCanvas]);

  useEffect(() => {
    fabricCanvas.observer('mouse:down', (options) => {
      fabricCanvas.drawItem(options.pointer);
      changeTool('select');
    });

    fabricCanvas.observer('object:added', (options) => {
      const added = options.target!;
      changeTool('select');

      added.on('selected', ({ target }) => {
        changeCurrentSelected(target!);
      });
    });

    return () => {
      fabricCanvas.removeObserver('mouse:down');
      fabricCanvas.removeObserver('object:added');
    };
  }, [changeCurrentSelected, changeTool, fabricCanvas]);

  const downloadImage = (event: React.MouseEvent) => {
    const target = event.target as HTMLButtonElement;
    target.classList.add('selected');
    setTimeout(() => target.classList.remove('selected'), 150);

    const data = fabricCanvas.toDataURL();
    const a = document.createElement('a');
    a.href = data;
    a.download = getNameWithNowDate('stibee_divider_');
    a.click();
  };

  return (
    <section className="generator">
      <div className="toolbox">
        <ToolSelector current={tool} changeTool={changeTool} />
        <DownloadBtn onClick={downloadImage} />
      </div>
      <div className="container">
        <CanvasBG width={width} height={height} />
        <canvas
          className="canvas"
          ref={canvasRef as React.MutableRefObject<HTMLCanvasElement>}
        >
          <p className="alt">
            지원되지 않는 브라우저 입니다.
            <br />
            최신 버전의 브라우저를 사용하세요.
            <br />
            (Chrome 브라우저 사용을 권장합니다.)
          </p>
        </canvas>
      </div>
      <Controller currentSelected={currentSelected} renderAll={renderAll} />
    </section>
  );
};

export default DividerGenerator;
