import React, { useState } from 'react';
import { CurvedLineType } from '../fabric/curvedLine';
import InputBox from './InputBox';

interface Props {
  item: CurvedLineType;
  renderAll: () => void;
}

const TextController = ({ item, renderAll }: Props) => {
  const [isStroke, setIsStroke] = useState(Boolean(item.stroke));
  const [isFilled, setIsFilled] = useState(Boolean(item.fill));
  const [strokeWidth, setStrokeWidth] = useState(item.strokeWidth);
  const [skewX, setSkewX] = useState(item.skewX || 0);
  const [skewY, setSkewY] = useState(item.skewY || 0);
  const [opacity, setOpacity] = useState(item.opacity * 100);

  return (
    <div className="controller-container">
      <div className="controller-main">
        <InputBox
          label="외곽선"
          className="controller-input"
          name="isFilled"
          type="checkbox"
          checked={isStroke}
          onChange={() => {}}
          onClick={() => {
            setIsStroke((prev: boolean) => {
              if (!prev) {
                item.set('stroke', 'black');
              } else {
                item.set('stroke', undefined);
              }
              renderAll();
              return !prev;
            });
          }}
        />
        <InputBox
          label="선 두께"
          className="controller-input"
          name="strokeWidth"
          type="range"
          value={strokeWidth}
          min={0}
          max={10}
          valueStep={0.1}
          onChange={(event: React.ChangeEvent) => {
            const value = Number((event.target as HTMLInputElement).value);
            item.set('strokeWidth', value);
            setStrokeWidth(value);
            renderAll();
          }}
        />
        <InputBox
          label="투명도"
          className="controller-input"
          name="opacity"
          type="range"
          value={opacity}
          min={0}
          max={100}
          onChange={(event: React.ChangeEvent) => {
            const value = Number((event.target as HTMLInputElement).value);
            item.set('opacity', value / 100);
            setOpacity(value);
            renderAll();
          }}
        />
        <InputBox
          label="기울임(가로)"
          className="controller-input"
          name="skewX"
          type="range"
          value={skewX}
          min={-50}
          max={50}
          onChange={(event: React.ChangeEvent) => {
            const value = Number((event.target as HTMLInputElement).value);
            item.set('skewX', value);
            setSkewX(value);
            renderAll();
          }}
        />
        <InputBox
          label="기울임(세로)"
          className="controller-input"
          name="skewY"
          type="range"
          value={skewY}
          min={-50}
          max={50}
          onChange={(event: React.ChangeEvent) => {
            const value = Number((event.target as HTMLInputElement).value);
            item.set('skewY', value);
            setSkewY(value);
            renderAll();
          }}
        />
        <InputBox
          label="채우기"
          className="controller-input"
          name="isFilled"
          type="checkbox"
          checked={isFilled}
          onChange={() => {}}
          onClick={() => {
            setIsFilled((prev: boolean) => {
              if (!prev) {
                item.set('fill', 'black');
              } else {
                item.set('fill', undefined);
              }
              renderAll();
              return !prev;
            });
          }}
        />
      </div>
      {/* <div className="controller-sub"></div> */}
    </div>
  );
};

export default TextController;
