import React from 'react';
import checkmark from '../assets/svg/check.svg';
interface Props extends React.InputHTMLAttributes<HTMLInputElement> {
  label?: string;
  className?: string;
  name?: string;
  value?: any;
  step?: number;
  valueStep?: number;
  onChange?: (event: React.ChangeEvent) => void;
}

const InputBox = ({
  label,
  className,
  name,
  type,
  value,
  onChange,
  step,
  valueStep,
  ...props
}: Props) => {
  const id = Math.floor(Date.now() * Math.random() * Math.pow(10, 3)).toString(16);
  return (
    <div className={`inputbox ${className} ${type}`}>
      <label className={`inputbox-label ${className}`} htmlFor={id}>
        {label}
      </label>
      <div className={`inputbox-wrapper ${className}`}>
        <input
          id={id}
          className={`inputbox-input ${className}`}
          name={name}
          type={type || 'text'}
          value={value}
          step={step}
          onChange={onChange}
          {...props}
        />
        {type === 'checkbox' && (
          <div
            className={`inputbox-input-checkbox ${className}`}
            onClick={props.onClick as (event: React.MouseEvent) => void}
          >
            {props.checked && (
              <div className={`inputbox-input-checkbox-mark ${className}`}>
                <img className="icon" src={checkmark} alt="check" />
              </div>
            )}
          </div>
        )}
      </div>
      {props.max && (
        <div className="inputbox-sub">
          <input
            className={`inputbox-sub-value ${className}`}
            type="number"
            value={value}
            onChange={onChange}
            min={props.min}
            max={props.max}
            step={valueStep}
          />
          <span className={`inputbox-sub-max ${className}`}> / {props.max}</span>
        </div>
      )}
    </div>
  );
};

export default InputBox;
