import * as React from 'react';
import { Link } from 'react-router-dom';

import lablogo from '../assets/images/stibee-logo@2x.png';
import stibeelogo from '../assets/images/landingStibee-PC@2x.png';

function SiteHeader() {
  return (
    <header className="site-header">
      <div className="site-header-inner">
        <h1 className="logo">
          <Link to="/">
            <img src={lablogo} alt="stibee 실험실" />
            <span>실험실</span>
          </Link>
        </h1>
        <ul className="menu">
          <Link
            className={`menu-item ${setActiveIfMatchOf('/stibeeLab-onBoard/domain')}`}
            to="/domain"
          >
            도메인 조회
          </Link>
          <Link
            className={`menu-item ${setActiveIfMatchOf('/stibeeLab-onBoard/divider')}`}
            to="/divider"
          >
            구분선 생성
          </Link>
        </ul>
        <h2 className="stibee-logo">
          <a href="https://stibee.com" target="_blank">
            <img src={stibeelogo} alt="stibee" />
          </a>
        </h2>
      </div>
    </header>
  );
}

function setActiveIfMatchOf(path: string) {
  return window.location.pathname === path ? 'active' : '';
}

export default SiteHeader;
